import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Player')
export class Player extends Component {
  @property(Number) movementSpeed: number = 0.01;

  public updatePlayerPosition(direction: Vec3) {
    const newPosition = this.node.getPosition().add(direction.multiplyScalar(this.movementSpeed));
    this.node.setPosition(newPosition);
  }
}


